import * as React from 'react'
import styled from '../styles/styled-components'
import { getEmSize } from '../styles/mixins'

const StyledContainer = styled.section`
  position: relative;
  width: 100%;
  padding-top: 54.6875%;

  ${p => p.theme.media.lg`
    min-height: ${getEmSize(700)}em;
  `};
`

const Contents = styled.div`
  position: absolute;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
`

interface CardContainerProps {
  className?: string
}

export const RatioContainer: React.SFC<CardContainerProps> = ({ className, children }) => (
  <StyledContainer>
    <Contents className={className}>{children}</Contents>
  </StyledContainer>
)



